<template>
    <div class="item-body">

        <div class="show-item">

            <figure class="view-item">
                <img class="image-item" :src="path_image" alt="polo">
            </figure>

            <div class="details-item">
                
                <div class="text-details-item">
                    <h1 class="item_name">{{name_item}}</h1>
                    <p class="item_price">S/. {{price_item}}</p>
                    
                    <p class="detail-text"><span class="bold-text">Marca:</span> {{name_marca}}</p>
                    <p class="detail-text"><span class="bold-text">Talla:</span> {{talla_item}}</p>
                    
                    <form id="div-cantItem">
                        <label for="cantItem">Cantidad:</label>
                        <input id="cantItem" type="number" name="cantItem" value="0" min="0">
                    </form>
                </div>

                <div class="actions">
                    <button type="button" class=" btn-action btn btn-primary btn-sd" @click="doShop">
                        Comprar<img class="ficon" src="../assets/icons/shopcar.svg" alt="shopcar icon">
                    </button>

                    <button type="button" class="btn-action btn btn-primary btn-sd" @click="goProbar">Probar</button>
                    <!--
                    <a class=" btn-action02 bton-special anchor" :href="'/try-on/'+id_item"><button type="button" class="btn btn-action03 btn-primary btn-sd">Probar</button></a>
                    -->
                </div>




            </div>
            
        </div>

        <div class="description">
            <div class="name-description">
                <p>Description</p>    
            </div>
            <div class="body-description">
                <p>{{description_item}}</p>
            </div>

        </div>

    </div>
</template>

<script>
import axios from 'axios'
import Swal from 'sweetalert2';

export default {
    name: 'NewItem',
    data(){
        return{
            cor_filters: [],
            marca_filters: [],
            talla_filters: [],
            name_cor: "",
            name_marca: "",
            description: "",
            path_image: "",
            description_item: "",
            name_item: "",
            price_item: "",
            talla_item: "",
            stock_item: "",
            id_item: "",
            compras: { "results" : []},
            userSession: false,
        }
    },
    mounted() {
        this.checkUserSession();
        this.getFilters();
        this.getListShop();
        this.getInfo();
    },
    methods: {
        goProbar(){
            location.href = '/try-on/'+(this.id_item).toString();
        },
        getFilters(){
            axios
                .get("/api/catalogo/filters/")
                .then(res => {
                    this.data = res.data;
                    this.cor_filters = this.data.colores;
                    this.marca_filters = this.data.marcas;
                    this.talla_filters = this.data.tallas;
                })
                .catch(error =>{
                    console.log(error)
                })
        },
        getInfo(){
            const item_id= this.$route.params.id;
            this.id_item = item_id;

            axios
                .get('/api/paginaItem/detailsPolo/'+item_id)
                .then(res => {
                    //console.log(res.data);
                    const data = res.data;
                    this.name_cor = this.cor_filters[data.color-1];
                    this.name_marca = this.marca_filters[data.marcas-1];
                    this.path_image = data.path_image;
                    this.description_item = data.description;
                    this.name_item = data.name_modelo;
                    this.price_item = data.price;
                    this.stock_item = data.stock;
                    this.talla_item = data.talla;
                })
                .catch(error => {
                    console.log(error);
                })
                
        },
        getListShop(){
            var items_shop = localStorage.getItem("startCompras");
            //console.log(items_shop);
            if(items_shop != null){
                items_shop = JSON.parse(items_shop);
                this.compras["results"] = items_shop["results"];
            }
        },
        checkUserSession(){
            const activeUserSession = localStorage.getItem("user_session");
            if(activeUserSession != null){
                this.userSession = true;
            }else{
                this.userSession = false;
            }
        },
        doShop(){
            const form = document.getElementById("cantItem");
            const detailsItem = {};

            if(form.value > 0){

                if(this.userSession){
                    
                    var tmp_details = localStorage.getItem("startCompras");
                    
                    if(tmp_details != null){
                        tmp_details = JSON.parse(tmp_details);

                        var isMyItem = false;

                        for(const e of tmp_details["results"]){ 
                            if(this.id_item == e["id"]){ 
                                console.log("si esta");
                                isMyItem = true;
                                e["cantidad"] = parseInt(e["cantidad"]) + parseInt(form.value);
                                break;
                            } 
                        }
                        
                        if(isMyItem == false){

                            detailsItem["id"] = this.id_item;
                            detailsItem["modelo"] = this.name_item;
                            detailsItem["marca"] = this.name_marca;
                            detailsItem["talla"] = this.talla_item;
                            detailsItem["precio"] = this.price_item;
                            detailsItem["cantidad"] = form.value;
                            detailsItem["get_image"] = this.path_image;

                            this.compras["results"].push(detailsItem);
                            localStorage.setItem("startCompras",JSON.stringify(this.compras));
                        }else{
                            localStorage.setItem("startCompras",JSON.stringify(tmp_details));
                        }

                    }else{

                        detailsItem["id"] = this.id_item;
                        detailsItem["modelo"] = this.name_item;
                        detailsItem["marca"] = this.name_marca;
                        detailsItem["talla"] = this.talla_item;
                        detailsItem["precio"] = this.price_item;
                        detailsItem["cantidad"] = form.value;
                        detailsItem["get_image"] = this.path_image;

                        this.compras["results"].push(detailsItem);
                        localStorage.setItem("startCompras",JSON.stringify(this.compras));

                    }

                    Swal.fire('Item Agregado!', 'Revise su carrito de compras','success');
                }else{
                    Swal.fire('Iniciar Sesi√≥n!', 'Tiene que loguearse para comprar un producto','error');
                }

            }else if(form.value <= 0){
                Swal.fire('Error!', 'Cantidad de items debe ser mayor a 0','error');
            }

            //console.log(this.compras)


            /*const cantData = new FormData(form);

            for (var pair of cantData.entries()) {
                console.log(pair[0]+ ', ' + pair[1]); 
            }*/

        },
        deleteCompras(){
            localStorage.removeItem("startCompras");
        },

    },
}
</script>

